"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { useSupabase } from "@/src/components/providers/supabase-provider"
import { createApiClient } from "@/src/lib/api/client"
import { DataTable } from "@/src/features/schedules/components/schedules-table" // Reuse table component
import { Button } from "@/components/ui/button"
import { Plus } from "lucide-react"
import { ColumnDef } from "@tanstack/react-table"
import { Badge } from "@/components/ui/badge"
import { format } from "date-fns"
import { TaskForm } from "@/src/features/tasks/components/task-form"

interface Task {
  id: string
  title: string
  taskDate: string
  status: 'pending' | 'completed' | 'canceled'
  jobName?: string
  technicianName?: string
  customerName?: string
}

export default function TasksPage() {
  const { session } = useSupabase()
  const router = useRouter()
  const [data, setData] = useState<Task[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  
  const [page, setPage] = useState(1)
  const [totalPages, setTotalPages] = useState(1)
  const [isFormOpen, setIsFormOpen] = useState(false)
  const [editingTask, setEditingTask] = useState<Task | undefined>(undefined)

  const fetchTasks = async () => {
    if (!session) return
    setLoading(true)
    setError(null)
    try {
      const api = createApiClient(session) as any
      const res = await api.tasks.list({ 
          page, 
          limit: 10 
      })
      setData(res.data)
      const total = res.meta?.total || 0
      setTotalPages(Math.ceil(total / 10))
    } catch (err: any) {
      console.error(err)
      setError("Failed to load tasks")
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchTasks()
  }, [session, page])

  const columns: ColumnDef<Task>[] = [
    {
      accessorKey: "taskDate",
      header: "Date & Time",
      cell: ({ row }) => {
          const date = new Date(row.original.taskDate)
          return (
              <div className="flex flex-col">
                  <span className="font-medium">{format(date, "PPP")}</span>
                  <span className="text-xs text-muted-foreground">{format(date, "p")}</span>
              </div>
          )
      }
    },
    {
       accessorKey: "title",
       header: "Title",
       cell: ({ row }) => <span className="font-medium text-[#0A2540]">{row.original.title}</span>
    },
    {
       accessorKey: "jobName",
       header: "Job Type",
       cell: ({ row }) => row.original.jobName || "-"
    },
    {
       accessorKey: "technicianName",
       header: "Technician",
       cell: ({ row }) => row.original.technicianName || "Unassigned"
    },
    {
      accessorKey: "status",
      header: "Status",
      cell: ({ row }) => {
          const status = row.original.status
          const colors: Record<string, string> = {
              pending: "bg-yellow-100 text-yellow-800",
              completed: "bg-green-100 text-green-800",
              canceled: "bg-gray-100 text-gray-800"
          }
          return (
             <Badge variant="secondary" className={colors[status] || ""}>
                 {status.toUpperCase()}
             </Badge>
          )
      }
    },
    {
      id: "actions",
      header: "Actions",
      cell: ({ row }) => (
        <Button variant="ghost" size="sm" onClick={(e) => {
            e.stopPropagation()
            setEditingTask(row.original)
            setIsFormOpen(true)
        }}>
            Edit
        </Button>
      )
    }
  ]

  const handleCreate = () => {
      setEditingTask(undefined)
      setIsFormOpen(true)
  }

  return (
    <div className="p-6 space-y-6">
      <div className="flex justify-between items-center">
        <div>
            <h1 className="text-2xl font-bold tracking-tight text-[#0A2540]">Tasks</h1>
            <p className="text-muted-foreground">Assign and manage technician tasks.</p>
        </div>
        <Button onClick={handleCreate} className="bg-[#0A2540] hover:bg-[#0A2540]/90">
            <Plus className="mr-2 h-4 w-4" /> New Task
        </Button>
      </div>

      <DataTable 
        columns={columns} 
        data={data} 
        isLoading={loading} 
        error={error}
        pagination={{
            pageIndex: page,
            pageSize: 10,
            pageCount: totalPages,
            onPageChange: setPage
        }}
        onRowClick={(row) => {
            setEditingTask(row)
            setIsFormOpen(true)
        }}
      />

      {isFormOpen && (
          <TaskForm 
            isOpen={isFormOpen}
            onClose={() => setIsFormOpen(false)}
            onSuccess={fetchTasks}
            initialData={editingTask}
          />
      )}
    </div>
  )
}
